import styles from "./register.module.css";
import {
  EmailInput,
  PasswordInput,
  Input,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { useDispatch } from "../../utils/type-hooks";
import { useForm } from "../../hooks/hooks";
import { registerUser } from "../../services/actions/current-user";
import ComponentAuth from "../../components/component-auth/component-auth";
import { Link } from "react-router-dom";

function Register() {
  const dispatch = useDispatch();

  const { inputValues, handleChange } = useForm({
    name: "",
    email: "",
    password: "",
  });

  function handleSubmit(evt: React.FormEvent<HTMLFormElement>) {
    evt.preventDefault();

    dispatch(registerUser(inputValues.name, inputValues.email, inputValues.password));
  }

  return (
    <div>
      <ComponentAuth
        title=""
        textButton=""
        formName="formRegister"
        handleSubmit={handleSubmit}
      >
        <Input
          placeholder={""}
          value={inputValues.name}
          onChange={handleChange}
          extraClass="mb-6"
          name="name"
        />
        <EmailInput
          isIcon={false}
          placeholder={"E-mail"}
          size={"default"}
          name={"email"}
          value={inputValues.email}
          onChange={handleChange}
          extraClass="mb-6"
        />
        <PasswordInput
          name={"password"}
          value={inputValues.password}
          onChange={handleChange}
        />
      </ComponentAuth>
      <p className="mt-20 text text_type_main-default text_color_inactive">
         ?{" "}
        <Link className={styles.link} type="submit" to="/login">
          
        </Link>
      </p>
    </div>
  );
}

export default Register;
